.rmdb-header {
	width: 100%;
	background: #1c1c1c;
	padding: 0 20px; }

.rmdb-header-content {
	max-width: 1280px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px 0px;
	margin: 0 auto; }

.rmdb-logo {
	width: 200px; }

.rmdb-tmdb-logo {
	width: 100px; }

@media screen and (max-width: 500px) {
	.rmdb-tmdb-logo {
		width: 80px; }

	.rmdb-logo {
		width: 150px; } }
